import {memo, React, useState} from "react";
import {useStoreActions, useStoreState} from "easy-peasy";
import {ReactComponent as WaitIcon} from "../icons/wait.svg";
import {Handle} from "react-flow-renderer";
import juration from "juration";
import {toTitleCase} from "../helpers";
import {Activity} from "../components/activity";
import {StartTriggerType, WaitType} from "./registry";
import i18n_en from "../i18n_en";
import {NodeDescription} from "../components/node-description";
import {HoverActions} from "../components/hover-actions";
import {DescriptionPane} from "../components/description-tab";
import {PropertyTabSelector} from "../components/property-tab-selector";
import {NodeTitle} from "../components/node-title";
import {PropertySection} from "../components/property-section";

export default {
    reactFlowNode: memo(props => {
        const [showOnHoverActions, setShowOnHoverActions] = useState(false);
        const {getNodeProperty} = useStoreState((state) => state);
        const description = getNodeProperty(props.id, 'description');
        const waitTypeProperties = getNodeProperty(props.id, 'properties');
        return (
            <>
                <div className={'node-content bg-white border-2 border-lcLabel hover:border-nodeBlue w-48 h-42'}
                     onMouseEnter={() => setShowOnHoverActions(true)}
                     onMouseLeave={() => setShowOnHoverActions(false)}>

                    <HoverActions id={props.id} show={showOnHoverActions} />

                    <div className={'inline-flex items-center p-1'}>
                        <WaitIcon className={'mr-3 w-4 fill-stroke text-nodeTitle'}/>
                        <label
                            className={'capitalize text-sm font-bold text-nodeTitle'}>{i18n_en.wait.wait}</label>
                    </div>

                    <hr/>

                    <div className={`flex justify-center mt-${waitTypeProperties ? '1' : '5'} pb-2 text-sm`}>
                        {waitTypeProperties?.repr}
                    </div>

                    <NodeDescription id={props.id} show={Boolean(description)}/>
                </div>

                <Handle
                    type="source"
                    position="bottom"
                    isConnectable={false}
                />
                <Handle
                    type="target"
                    position="top"
                    isConnectable={false}
                />

            </>
        );
    }),
    widget: memo(() => {
        // TODO: Considered disabling when the last item or the selected item is of same (Wait) type,
        // as adding delay to delay makes little sense. But on the other hand, the user can be free.
        const {rfInstances, selectedRfElement} = useStoreState((state) => state);
        const isDisabled = selectedRfElement?.type === WaitType || !rfInstances.some(n => n.type === StartTriggerType);
        return <Activity text={i18n_en.wait.wait}
                         type={WaitType}
                         icon={WaitIcon}
                         IsDisabled={isDisabled}/>;
    }),
    propertiesView: memo((props) => {
            const [activeTab, setActiveTab] = useState('settings');
            const [waitText, setWaitText] = useState(undefined);
            const {getNodeProperty} = useStoreState((state) => state);
            const {updateNodeProperty} = useStoreActions((actions) => actions);

            const waitTypeProperties = getNodeProperty(props.id, 'properties');

            const displayWaitText = (text) => {
                try {
                    return juration.stringify(juration.parse(text), {format: 'long'});
                } catch {
                    return '0';
                }
            };

            return (
                <>
                    <NodeTitle title={`${i18n_en.wait.wait}-${props.id}`}/>
                    <PropertyTabSelector selectedTab={activeTab} onClick={setActiveTab}/>
                    <PropertySection name={i18n_en.configuration} />

                    {activeTab === 'settings' &&
                    <div className='flex flex-col ml-3 mr-3'>
                        <label className='text-sm'>{toTitleCase(i18n_en.wait.wait_input)}</label>
                        <input className='w-full border-2 border-lcLine py-1 text-sm mb-4  p-1'
                               placeholder={i18n_en.wait.wait_input_example}
                               defaultValue={waitTypeProperties?.value}
                               onChange={(e) => e.target.value === '' ? 0 : setWaitText(e.target.value)}/>
                        <label
                            className={`capitalize text-sm ring-2 ring-${displayWaitText(waitText) === '0' ? 'red-500' : 'green-500'} rounded-full p-2`}> = {displayWaitText(waitText)}</label>

                        <ul className='opacity-50 mt-2'>
                            <li>12.4 secs</li>
                            <li>3 mins 4 sec</li>
                            <li>2 hrs 20 min</li>
                            <li>2h20min</li>
                            <li>6 mos 1 day</li>
                            <li>47 yrs 6 mos and 4d</li>
                            <li>3 weeks and 2 days</li>
                        </ul>

                        <div className='flex justify-end mt-6 space-x-10'>
                            <button className='capitalize'>{i18n_en.cancel}</button>
                            <button
                                onClick={() => updateNodeProperty({
                                    id: props.id,
                                    props: {
                                        properties: {
                                            value: waitText,
                                            repr: displayWaitText(waitText)
                                        }
                                    }
                                })}
                                className='disabled:opacity-50 capitalize px-3 py-1 bg-btnSuccess text-white font-bold'
                                // TODO: disabled is incomplete here
                                disabled={waitText === waitTypeProperties?.value || waitText === '0'}>{i18n_en.save}</button>
                        </div>
                    </div>
                    }
                    <DescriptionPane id={props.id} show={activeTab === 'description'}/>
                </>
            );
        }
    ),
    editorGroup: 2
}