import {ReactComponent as NodeEditIcon} from "../icons/edit.svg";
import {ReactComponent as NodeDeleteIcon} from "../icons/delete.svg";
import {useStoreActions} from "easy-peasy";

export const HoverActions = ({id, show}) => {
    const {removeElement} = useStoreActions((actions) => actions);
    return (
        <>
            {show &&
            <div className={'inline-flex space-x-2 absolute right-2 -top-5 bg-nodeBlue p-2'}>
                <button className='focus:outline-none' onClick={() => console.log(id)}><NodeEditIcon
                    className={'w-3.5 fill-current fill-stroke text-white'}/></button>
                <button className='focus:outline-none' onClick={(evt) => {
                    removeElement(id);
                    evt.stopPropagation();
                }}><NodeDeleteIcon
                    className={'w-3.5 fill-current fill-stroke text-white'}/></button>
            </div>
            }
        </>
    );
};