import entry from './entry';
import email from './send-email';
import sms from './send-sms';
import delay from './wait';
import addNew from './new';
import customEdge from './custom-edge';

export const StartTriggerType = 'entry';
export const StopWorkflowType = 'stop';
export const LinkWorkflowType = 'linkWorkflow';
export const SendEmailType = 'sendEmail';
export const SendSmsType = 'sendSms';
export const ConditionType = 'condition';
export const WaitType = 'wait';
export const ScheduleType = 'schedule';
export const TagType = 'tag';
export const AddNewType = 'new';

export const registry = {
    [StartTriggerType]: entry,
    [SendEmailType]: email,
    [SendSmsType]: sms,
    [WaitType]: delay
};

export function hasProperties(nodeType) {
    return Object.entries(registry).some(([k, v]) => {
        return k === nodeType && v.hasOwnProperty('propertiesView');
    });
}

export const nodeTypes = Object.entries(registry).reduce((cur, [k, v]) => {
    cur[k] = v.reactFlowNode;
    return cur;
}, {[AddNewType]: addNew});

export const edgeTypes = {
    custom: customEdge,
};