import {action, computed} from "easy-peasy";
import {getConnectedEdges, getIncomers, isEdge, isNode, removeElements, updateEdge} from "react-flow-renderer";
import dagre from "dagre";
import {nanoid} from "nanoid";
import {AddNewType, hasProperties} from "./elements/registry";

const {createStore} = require("easy-peasy");

const applyRfInstanceDefaults = (node) => {
    node.draggable = false;
    if (isEdge(node)) {
        node.type = 'custom';
    }
    if (isNode(node)) {
        node.dagreStyle = node.type === AddNewType ? {width: 290} : {width: 400};
        node.dagreStyle.height = 200;
        node.position = {x: 0, y: 0};
    }
    return node;
};

const applyRfAutoLayout = (elements) => {
    const direction = 'TB';
    const dagreGraph = new dagre.graphlib.Graph();
    dagreGraph.setDefaultEdgeLabel(() => ({}));
    dagreGraph.setGraph({rankdir: direction});


    elements.forEach((el) => {
        if (isNode(el)) {
            dagreGraph.setNode(el.id, {width: el.dagreStyle.width, height: el.dagreStyle.height});
        } else {
            dagreGraph.setEdge(el.source, el.target);
        }
    });
    dagre.layout(dagreGraph);
    return elements.map((el) => {
        if (isNode(el)) {
            el = {...el};
            const nodeWithPosition = dagreGraph.node(el.id);
            // unfortunately we need this little hack to pass a slightly different position
            // in order to notify react flow about the change
            el.position = {
                x: nodeWithPosition.x - el.dagreStyle.width / 2 + Math.random() / 1000,
                y: nodeWithPosition.y - el.dagreStyle.height / 2
            };
        }
        return el;
    });
};

const store = createStore({
        selectedRfElement: null,
        showActionsPanel: true,
        rfInstances: [],

        updateNodeProperty: action((state, {id, props}) => {
            let current = state.rfInstances.find(i => i.id === id);
            state.rfInstances[state.rfInstances.indexOf(current)] = {...current, ...props};
        }),

        getNodeProperty: computed(state => (id, property) => {
            const current = state.rfInstances.find(i => i.id === id);
            if (current) {
                return current[property];
            }
        }),

        removeElement: action((state, id) => {
            const elementToRemove = state.rfInstances.find(i => i.id === id);
            const edges = getConnectedEdges([elementToRemove], state.rfInstances.filter(i => isEdge(i)));

            const edgeIn = edges.find(e => e.target === id);
            const edgeOut = edges.find(e => e.source === id);

            const outNode = state.rfInstances.find(i => i.id === edgeOut.target);

            let edge;
            if (outNode.type === AddNewType) {
                edge = {
                    id: nanoid(8),
                    source: edgeIn.source,
                    target: edgeOut.target,
                    style: {strokeDasharray: '5'}
                };
            } else {
                edge = {
                    id: nanoid(8),
                    source: edgeIn.source,
                    target: edgeOut.target,
                    type: 'custom'
                };
            }

            // TODO: The correct way here is setSelectedRfElement(null), but it is not very easy to call action from action.
            state.showActionsPanel = true;
            state.selectedRfElement = null;

            const instances = removeElements([elementToRemove], state.rfInstances);
            instances.push(edge);
            state.rfInstances = applyRfAutoLayout(instances);
        }),

        setSelectedRfElement: action((state, element) => {
            if (element) {
                element = state.rfInstances.find(i => i.id === element.id);
                if (isNode(element)) {
                    state.selectedRfElement = element;
                    hasProperties(element.type) ? state.showActionsPanel = false : state.showActionsPanel = true;
                } else if (isEdge(element)) {
                    state.selectedRfElement = element;
                    state.showActionsPanel = true;
                }
            } else {
                state.showActionsPanel = true;
                state.selectedRfElement = null;
            }
        }),

        setShowActionsPanel: action((state, show) => {
            state.showActionsPanel = show;
        }),

        addNewNode: action((state, type) => {
            // TODO: deleting the trigger should delete the whole workflow, consider a warning dialog
            // TODO: Known issue -> add entry -> add delay -> click on delay -> add email => the diagram brakes. This happens, when an element does not have properties, hence can be selected + added item. Start trigger is exception

            const newNode = applyRfInstanceDefaults({
                id: nanoid(8),
                type: type
            });

            if (state.rfInstances.length === 0) {
                const specialNode = applyRfInstanceDefaults({
                    id: nanoid(8),
                    type: AddNewType,
                    data: {},
                });

                const specialEdge = {
                    id: nanoid(8),
                    source: newNode.id,
                    target: specialNode.id,
                    style: {strokeDasharray: '5'}
                };
                state.rfInstances.push(newNode, specialNode, specialEdge);
                state.rfInstances = applyRfAutoLayout(state.rfInstances);
            } else {
                const special = state.rfInstances.find(i => i.type === AddNewType);

                if (state.selectedRfElement === null) {
                    let previous = getIncomers(special, state.rfInstances)[0];
                    const newEdge = {
                        id: nanoid(8),
                        source: previous.id,
                        target: newNode.id,
                        type: 'custom'
                    };

                    state.rfInstances.push(newNode, newEdge);
                    const edge = getConnectedEdges([special], state.rfInstances.filter(i => isEdge(i)))[0];
                    state.rfInstances = updateEdge(edge, {source: newNode.id, target: special.id}, state.rfInstances);
                    state.rfInstances = applyRfAutoLayout(state.rfInstances);
                } else {
                    if (isEdge(state.selectedRfElement)) {
                        state.rfInstances = removeElements([state.selectedRfElement], state.rfInstances);

                        const edgeInNewNode = {
                            id: nanoid(8),
                            source: state.selectedRfElement.source,
                            target: newNode.id,
                            type: 'custom'
                        };

                        const edgeOutNewNode = {
                            id: nanoid(8),
                            source: newNode.id,
                            target: state.selectedRfElement.target,
                            type: 'custom'
                        };

                        state.rfInstances.push(newNode, edgeInNewNode, edgeOutNewNode);
                        state.rfInstances = applyRfAutoLayout(state.rfInstances);
                    } else if (isNode(state.selectedRfElement)) {
                        if (state.selectedRfElement.type === AddNewType) {
                            const edge = getConnectedEdges([special], state.rfInstances.filter(i => isEdge(i)))[0];

                            const newEdge = {
                                id: nanoid(8),
                                source: edge.source,
                                target: newNode.id,
                                type: 'custom'
                            };
                            state.rfInstances.push(newNode, newEdge);
                            state.rfInstances = updateEdge(edge, {
                                source: newNode.id,
                                target: special.id
                            }, state.rfInstances);
                            state.rfInstances = applyRfAutoLayout(state.rfInstances);
                        } else {
                            const newEdge = {
                                id: nanoid(8),
                                source: state.selectedRfElement.id,
                                target: newNode.id,
                                type: 'custom'
                            };

                            state.rfInstances.push(newNode, newEdge);

                            const edge = getConnectedEdges([state.selectedRfElement], state.rfInstances.filter(i => isEdge(i)))[0];
                            state.rfInstances = updateEdge(edge, {
                                source: newNode.id,
                                target: edge.target
                            }, state.rfInstances);

                            state.rfInstances = applyRfAutoLayout(state.rfInstances);
                        }

                    }
                }
            }
        }),
    },
    {
        initialState: {}
    }
    )
;

export default store;