import React from "react";
import Editor from "@monaco-editor/react";
import {useStoreState} from "easy-peasy";
import {useEffect, useRef, useCallback} from "react";

export const CodeEditor = (props) => {
    const {rfInstances} = useStoreState((state) => state);

    const editorRef = useRef(null);

    const onLoad = useCallback((editor, monaco) => {
        editorRef.current = editor;
    }, [editorRef]);

    useEffect(() => {
        if (!props.show && editorRef.current) {
            console.log(JSON.parse(editorRef.current.getValue()));
        }
        if (props.show) {
            editorRef.current.getAction('editor.action.formatDocument').run()
            editorRef.current.setPosition({column: 1, lineNumber: 1});
            editorRef.current.focus();
        }
    }, [props.show, editorRef]);

    return (
        <>
            <div className={`absolute m-20 z-50 ${props.show ? 'visible' : 'hidden'}`}>
                <Editor
                    height="70vh"
                    width="35vw"
                    defaultLanguage="json"
                    value={JSON.stringify(rfInstances)}
                    loading={''}
                    theme='vs-dark'
                    onMount={onLoad}
                    options={{formatOnType: true, minimap: {enabled: false}, scrollbar: {vertical: 'hidden'}}}
                />
            </div>
        </>
    );
}