import {useStoreActions} from "easy-peasy";

export const Activity = ({text, icon, type, IsDisabled}) => {
    const {addNewNode} = useStoreActions(actions => actions);
    const Icon = icon;
    return (
        <button disabled={IsDisabled} onClick={() => addNewNode(type)}
                className="disabled:opacity-50 border-2 w-28 h-28 inline-flex flex-col items-center place-content-center shadow">
            <Icon className='w-10 h-10 text-primary'/>
            <h1 className='pt-3 text-primary text-sm capitalize'>{text}</h1>
        </button>
    );
};