import React, {useCallback, useState} from "react";
import {useStoreActions, useStoreState} from "easy-peasy";
import {useEffect} from "react";
import ReactFlow, {
    Background,
    Controls,
    MiniMap
} from "react-flow-renderer";
import {edgeTypes, registry, nodeTypes, AddNewType} from "./elements/registry";
import i18n_en from "./i18n_en";
import {CodeEditor} from "./components/code-editor";
import {SidebarProvider} from "./components/sidebar-provider";
import {ReactComponent as EditIcon} from "./icons/edit.svg";


const App = () => {
    const {setSelectedRfElement} = useStoreActions((actions) => actions);
    const {showActionsPanel} = useStoreState((state) => state);

    const [reactFlowInstance, setReactFlowInstance] = useState(null);
    const [showEditor, setShowEditor] = useState(false);
    const {rfInstances} = useStoreState((state) => state);

    const onLoad = useCallback((instance) => {
        if (!reactFlowInstance) {
            setReactFlowInstance(instance);
        }
    }, [reactFlowInstance, setReactFlowInstance]);

    useEffect(() => {
        if (reactFlowInstance && rfInstances.length > 0) {
            reactFlowInstance.fitView();
        }
    }, [reactFlowInstance, rfInstances]);

    return (
        <>
            <div>
                <button className='absolute z-50 left-2 top-20 p-1 ring-blue-300 ring-1'
                        onClick={() => setShowEditor(!showEditor)}><EditIcon
                    className='w-7 text-blue-500'/></button>
                {<CodeEditor show={showEditor}/>}
            </div>

            <div className='grid grid-cols-app' style={{height: 'calc(100vh - 56px)'}}>
                <ReactFlow
                    className='border-r-2 border-lcLine bg-lcEditor'
                    elements={rfInstances}
                    nodeTypes={nodeTypes}
                    edgeTypes={edgeTypes}
                    maxZoom={1}
                    minZoom={1}
                    panOnScrollMode='vertical'
                    panOnScroll={true}
                    paneMoveable={false}
                    onLoad={onLoad}
                    onSelectionChange={(el) => el ? setSelectedRfElement(el[0]) : setSelectedRfElement(null)}
                >
                    {rfInstances.length > 0 &&
                    <Controls showInteractive={false} showZoom={false}/>
                    }

                    {/* TODO: Is there a way to pass here color as argument? Use theme function from tailwind  */}
                    <Background color='#FAFAFA'/>
                    {rfInstances.length > 0 &&
                    <MiniMap
                        nodeColor={(node) => {
                            switch (node.type) {
                                case 'entry':
                                    return 'green';
                                case AddNewType:
                                    return 'red';
                                default:
                                    return '#eeeeee';
                            }
                        }}
                        nodeStrokeColor={(n) => {
                            if (n.type === 'entry') return '#green';
                            if (n.type === AddNewType) return 'red';
                        }}
                    />
                    }
                </ReactFlow>

                {!showActionsPanel && <SidebarProvider/>}
                {showActionsPanel &&
                <div>
                    <h1 className='mt-3 mb-6 pb-3 pl-3 text-lg border-b-2 border-lcLine text-primary capitalize'>{i18n_en.actions}</h1>
                    <div className="mx-8 gap-y-3 grid col-start-2 grid-cols-3 justify-items-center">
                        {Object.entries(registry).filter(([k, v]) => v.editorGroup === 1).map(entry => {
                            const Node = entry[1].widget;
                            return <Node key={entry[0]} workflow={{data: []}}/>
                        })}

                        <div className='row col-span-full h-3'/>

                        {Object.entries(registry).filter(([k, v]) => v.editorGroup === 2).map(entry => {
                            const Node = entry[1].widget;
                            return <Node key={entry[0]} workflow={{data: []}}/>
                        })}
                    </div>
                </div>}
            </div>
        </>);
};

export default App;
