import i18n_en from "../i18n_en";
import {React, useState} from "react";
import {useStoreActions, useStoreState} from "easy-peasy";

export const DescriptionPane = ({id, show}) => {
    const {updateNodeProperty} = useStoreActions((actions) => actions);
    const {getNodeProperty} = useStoreState((state) => state);
    const [workflowDescription, setWorkflowDescription] = useState(undefined);

    const description = getNodeProperty(id, 'description');
    return (
        <>
            {show &&
            <div className='px-5'>
                <label className='capitalize text-sm'>{i18n_en.properties.description}</label>
                <textarea onChange={(a) => setWorkflowDescription(a.target.value)}
                          placeholder={i18n_en.description.placeholder}
                          defaultValue={description}
                          className='w-full mt-2 h-32 text-sm shadow-inner border-2 border-lcLine p-1'/>

                <div className='flex justify-end mt-10 space-x-10'>
                    <button className='capitalize'>{i18n_en.cancel}</button>
                    <button
                        disabled={workflowDescription === description}
                        onClick={() => updateNodeProperty({
                            id: id,
                            props: {description: workflowDescription}
                        })}
                        className='disabled:opacity-50 capitalize px-3 py-1 bg-btnSuccess text-white font-bold'>{i18n_en.save}</button>
                </div>
            </div>
            }</>
    );
};