import {getEdgeCenter, getMarkerEnd, getSmoothStepPath} from "react-flow-renderer";
import {ReactComponent as AddActionIcon} from "../icons/add_action.svg";

export default function CustomEdge({
                               id,
                               sourceX,
                               sourceY,
                               targetX,
                               targetY,
                               sourcePosition,
                               targetPosition,
                               style = {},
                               arrowHeadType,
                               markerEndId,
                           }) {
    const edgePath = getSmoothStepPath({
        sourceX, sourceY, sourcePosition, targetX, targetY, targetPosition
    });
    const markerEnd = getMarkerEnd(arrowHeadType, markerEndId);
    const [centerX, centerY, offsetX, offsetY] = getEdgeCenter({sourceX, sourceY, targetX, targetY});
    return (
        <>
            <path id={id} style={style} className="react-flow__edge-path" d={edgePath} markerEnd={markerEnd}/>

            <foreignObject x={centerX - 32 / 2} y={centerY - 32 / 2} width='2rem' height='2rem'>
                <AddActionIcon
                    className='edge-content w-8 h-8 border-2 rounded-full bg-lcLine fill-current text-white -r-2'/>
            </foreignObject>
        </>
    );
}