import {useStoreState} from "easy-peasy";
import {registry} from "../elements/registry";

export const SidebarProvider = () => {
    const {selectedRfElement} = useStoreState((state) => state);
    const PropView = registry[Object.keys(registry).find(k => k === selectedRfElement.type)].propertiesView;
    return <div><PropView key={selectedRfElement.id} id={selectedRfElement.id}/></div>;
};