import {memo, React, useRef, useState} from "react";
import {useStoreActions, useStoreState} from "easy-peasy";
import {ReactComponent as SendSmsIcon} from "../icons/send_sms2.svg";
import i18n_en from "../i18n_en";
import {Handle} from "react-flow-renderer";
import {toTitleCase} from "../helpers";
import {Activity} from "../components/activity";
import {SendSmsType, StartTriggerType} from "./registry";
import {NodeDescription} from "../components/node-description";
import {HoverActions} from "../components/hover-actions";
import {DescriptionPane} from "../components/description-tab";
import {PropertyTabSelector} from "../components/property-tab-selector";
import {NodeTitle} from "../components/node-title";
import {PropertySection} from "../components/property-section";

export default {
    reactFlowNode: memo((props) => {
        const [showOnHoverActions, setShowOnHoverActions] = useState(false);
        const {getNodeProperty} = useStoreState((state) => state);
        const description = getNodeProperty(props.id, 'description');
        return (
            <>
                <div className={'node-content bg-white border-2 border-lcLabel hover:border-nodeBlue w-48 h-42'}
                     onMouseEnter={() => setShowOnHoverActions(true)}
                     onMouseLeave={() => setShowOnHoverActions(false)}>

                    <HoverActions id={props.id} show={showOnHoverActions}/>

                    <div className={'inline-flex items-center p-1'}>
                        <SendSmsIcon className={'mr-3 w-5 fill-stroke text-nodeTitle'}/>
                        <label
                            className={'capitalize text-sm font-bold text-nodeTitle'}>{i18n_en.send_sms.send_sms}</label>
                    </div>
                    <hr/>
                    <div className={'flex justify-center mt-2 pb-2'}>
                        <SendSmsIcon
                            className='w-14 border-2 border-nodeContentBorder px-1 py-6 fill-stroke text-nodeContentBorder'/>
                    </div>

                    <NodeDescription id={props.id} show={Boolean(description)}/>
                </div>

                <Handle
                    type="source"
                    position="bottom"
                />
                <Handle type="target" position="top" isConnectable={false}/>
            </>
        );
    }),
    widget: memo(() => {
        const {rfInstances} = useStoreState((state) => state);
        const isDisabled = !rfInstances.some(n => n.type === StartTriggerType);
        return <Activity text={i18n_en.send_sms.send_sms}
                         type={SendSmsType}
                         icon={SendSmsIcon}
                         IsDisabled={isDisabled}/>;
    }),
    propertiesView: memo((props) => {
            const [activeTab, setActiveTab] = useState('settings');
            const [selectedSmsType, setSelectedSmsType] = useState(null);
            const [selectedSmsType2, setSelectedSmsType2] = useState(null);

            //TODO: description save and load is not working properly
            const formRef = useRef();

            return (
                <>
                    <NodeTitle title={`${i18n_en.sms.send_sms}-${props.id}`}/>
                    <PropertyTabSelector selectedTab={activeTab} onClick={setActiveTab}/>
                    <PropertySection name={i18n_en.configuration} />

                    {activeTab === 'settings' &&
                    <div className='flex flex-col ml-3 mr-3'>
                        <label className='capitalize text-sm'>{i18n_en.sms.sms_alternatives}</label>
                        {/* TODO: On options `capitalize` does not work, will use a util */}
                        <select required defaultValue='' id="sms_type"
                                className='__tmp_fix_select border-2 border-lcLine w-full mt-2 py-1 mb-4'
                                onChange={(a) => {
                                    setSelectedSmsType(a.target.value);
                                    setSelectedSmsType2(null);
                                }}>
                            <option value='' hidden>Select</option>
                            <option className='text-sm'
                                    value="1">{toTitleCase(i18n_en.sms.sms_trigger_template)}</option>
                            <option className='text-sm'
                                    value="2">{toTitleCase(i18n_en.sms.configure_new_sms)}</option>
                            {/*  TODO: capitalize this string here using css of tailwind. report that all capital does not look correct*/}
                        </select>

                        {selectedSmsType === '1' &&
                        <div className='mb-3'>
                            <label className='capitalize text-sm'>{i18n_en.sms.select_sms_trigger_template}</label>
                            <select required defaultValue='' id="sms_trigger_template"
                                    className='__tmp_fix_select border-2 border-lcLine w-full mt-2 py-1 mb-4'
                                    onChange={(a) => {
                                        setSelectedSmsType2(a.target.value);
                                    }
                                    }>
                                <option value='' hidden>Select</option>
                            </select>

                            <label className='capitalize text-sm'>{i18n_en.sms.select_sms_trigger_template}</label>
                            <p className='mt-3 text-sm'>{i18n_en.sms.sms_trigger_template_hint}</p>
                            <a href='#'
                               className='mt-2 text-sm text-btnSelectedText'>{i18n_en.sms.sms_trigger_template_hint_link}</a>
                        </div>
                        }

                        {selectedSmsType === '2' &&
                        <form ref={formRef}>
                            {/*/ API call Email/contenttype though the call gives three options, but in scetches, 2*/}
                            <label className='capitalize text-sm'>{i18n_en.sms.sms_content}</label>
                            <select required defaultValue='' id="sms_type2"
                                    className='__tmp_fix_select border-2 border-lcLine w-full mt-2 py-1 mb-4'
                                    onChange={(a) => setSelectedSmsType2(a.target.value)}>
                                <option value='' hidden>Select</option>
                                <option className='text-sm'
                                        value="3">{toTitleCase(i18n_en.sms.sms)}</option>
                                <option className='text-sm'
                                        value="4">{toTitleCase(i18n_en.sms.sms_with_landing_page)}</option>
                                {/*  TODO: capitalize this string here using css of tailwind
                                     report that all capital does not look correct
                                */}
                            </select>
                        </form>
                        }

                        <div className='flex justify-end mt-6 space-x-10'>
                            {/*TODO: What does cancel do? Deletes the node?*/}
                            <button className='capitalize'>{i18n_en.cancel}</button>
                            <button
                                className='disabled:opacity-50 capitalize px-3 py-1 bg-btnSuccess text-white font-bold'
                                disabled={true}
                                onClick={() => console.log(formRef.current)}>{i18n_en.save}</button>
                        </div>
                    </div>
                    }
                    <DescriptionPane id={props.id} show={activeTab === 'description'}/>
                </>
            );
        }
    ),
    editorGroup: 2
};